  margin-bottom: 1rem;
}

.c-footer .box-podcast .box-podcast--flecha img {
  width: 70px;
}

.c-footer .box-podcast .box-podcast--icono {
  display: flex;
  justify-content: flex-end;
  margin-right: 1rem;
}

.c-footer .box-podcast .box-podcast--icono img {
  width: 120px;
}

.c-footer .box-podcast .box-podcast--icono a {
  margin-top: 1.2rem;
  display: block;
  text-align: center;
  color: white;
  font-weight: bold;
  font-size: 1.5rem;
}

.c-footer .logo img {
  width: 200px;
}

.c-footer ul.footer-links li {
  margin: 5px 0;
}

.c-footer ul.footer-links li a {
  color: white;
}

.c-footer ul.footer-social li {
  margin: 0 5px;
}

.c-footer ul.footer-social li a {
  color: white;
}

.c-footer ul.footer-social li a i {
  font-size: 1.5rem;
}
